import React from 'react';

import Button from '@material-ui/core/Button';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Grow from '@material-ui/core/Grow';
import Paper from '@material-ui/core/Paper';
import Popper from '@material-ui/core/Popper';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import { makeStyles } from '@material-ui/core/styles';

import {
    Link
} from "react-router-dom";
import "../styles/header.css";

import MenuDrawer from "./MenuDrawer"
import clsx from 'clsx';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import logoImg from './../images/logo.png';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    popper: {
        top: '10px !important',
        zIndex: 1000,
    },
    appBar: {
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    toolBar: {
        height: '200%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
    },
    toolBarItems: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    menuItem: {
        fontFamily: "'Raleway', sans-serif",
        borderRadius: 4,
        margin: 5,
        color: 'white',
        textTransform: 'none',
        fontSize: 'initial',
    },
    menuSubItem: {
        fontFamily: "'Raleway', sans-serif",
        borderRadius: 4,
        margin: 5,
        color: '#da251c',
    },
    cntLogo: {
        display: 'flex',
        alignItems: 'center',
        '&:hover': {
            background: 'inherit'
        }
    },
    submenuList: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        backgroundColor: '#da251c',
        borderRadius: 4,
        padding: 5,
        border: 'none',
    },
    itemMenu: {
        fontFamily: "'Raleway', sans-serif",
        margin: 0,
        marginTop: 5,
        textDecoration: 'none',
        textAlign: 'center',
        color: 'white',
        padding: '6px 16px',
        width: 118,
        textTransform: 'none',
        fontSize: 'initial'
    }
}));

const Header = () => {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);

    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
    };

    const handleClose = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }

        setOpen(false);
    };

    function handleListKeyDown(event) {
        if (event.key === 'Tab') {
            event.preventDefault();
            setOpen(false);
        }
    }

    // return focus to the button when we transitioned from !open -> open
    const prevOpen = React.useRef(open);
    React.useEffect(() => {
        if (prevOpen.current === true && open === false) {
            anchorRef.current.focus();
        }

        prevOpen.current = open;
    }, [open]);

    return (
        <div className="cnt-header">
            <div className="normal">
                <AppBar
                    position="fixed"
                    className={clsx(classes.appBar, {
                        [classes.appBarShift]: open,
                    })}
                >
                    <Toolbar className={classes.toolBar}>
                        <Button className={classes.menuItem} href="/">
                            <img className='logoImg' src={logoImg} />
                        </Button>
                        <div className={classes.toolBarItems}>
                            <Button className={classes.menuItem} href="/">
                                Inicio
                            </Button>

                            <Button
                                className={classes.menuItem}
                                ref={anchorRef}
                                aria-controls={open ? 'menu-list-grow' : undefined}
                                aria-haspopup="true"
                                onClick={handleToggle}
                            >
                                Institucional
                            </Button>

                            <Button className={classes.menuItem} href="/contacto">
                                Contacto
                            </Button>

                        </div>
                    </Toolbar>
                </AppBar>
                <div className="cnt-popper">
                    <Popper className={classes.popper} open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
                        {({ TransitionProps, placement }) => (
                            <Grow
                                {...TransitionProps}
                                style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
                            >
                                <Paper>
                                    <ClickAwayListener onClickAway={handleClose}>
                                        <div className={classes.toolBarItems}>
                                            <MenuList className={classes.submenuList} autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                                                <Button onClick={handleClose} className={classes.itemMenu} href="/historia">
                                                    Historia
                                                </Button>
                                                <Button onClick={handleClose} className={classes.itemMenu} href="/comision">
                                                    Comisin
                                                </Button>
                                                <Button onClick={handleClose} className={classes.itemMenu} href="/principios">
                                                    Principios
                                                </Button>
                                            </MenuList>
                                        </div>
                                    </ClickAwayListener>
                                </Paper>
                            </Grow>
                        )}
                    </Popper>
                </div>
            </div>
            <div className="anormal">
                <MenuItem className="menuDrawer" style={{ display: "none" }}>
                    <MenuDrawer />
                </MenuItem>
            </div>

        </div>
    );
}

export default Header;